"use strict";
/* eslint-disable no-unused-vars */

import CustomEvents from 'core/custom_interaction_events';
import Modal from 'core/modal';
import ModalRegistry from 'core/modal_registry';
import {initDb} from "./database";

let registered = false;

let SELECTORS = {
    CANCEL_BUTTON: '[data-action="close"]',
};

/**
 * @constructor
 * @param {object} root The root jQuery element for the modal
 */
function ModalJqshow(root) {
    Modal.call(this, root);
}

ModalJqshow.TYPE = 'mod_kuet-modal_jqshow';
ModalJqshow.prototype = Object.create(Modal.prototype);
ModalJqshow.prototype.constructor = ModalJqshow;

ModalJqshow.prototype.registerEventListeners = function() {
    Modal.prototype.registerEventListeners.call(this);
    this.getModal().on(CustomEvents.events.activate, SELECTORS.CANCEL_BUTTON, function() {
        this.destroy();
    }.bind(this));
};

if (!registered) {
    ModalRegistry.register(ModalJqshow.TYPE, ModalJqshow, 'mod_kuet/modal_jqshow');
    registered = true;
}

export const modalJqshow = (region) => {
    return new ModalJqshow(region);
};

export default {
    modalJqshow
};
